html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
}

.main {
  flex: 1 1 auto;
}

img {
  display: block;
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
  color: inherit;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

ul, ol {
  list-style: none;
  margin: 0;
  padding: 0;
}

.noscroll {
  overflow: hidden;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container-header {
  box-sizing: content-box;
  margin: 0 auto;
  padding: 0 40px;
  max-width: 1800px;
  position: relative;

  @include width-1200 {
    padding: 0 20px;
  }
}

.container-title {
  padding: 0 10px;
  max-width: 1870px;
  & img {
    position: absolute;
    width: 100%;
    left: 0;
    right: 0;
    margin: 0 auto;
  }

  @include width-768 {
    & img {
      max-width: 304px;
    }
  }
}

.container {
  box-sizing: content-box;
  margin: 0 auto;
  padding: 0 80px;
  max-width: 1400px;
  position: relative;

  @include width-1200 {
    padding: 0 40px;
  }
  @include width-768 {
    padding: 0 20px;
  }
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

// -------------------------------

.btn-green {
  display: block;
  width: 100%;
  max-width: 300px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: none;
  border: none;
  padding: 18px 0;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: #FFFFFF;
  background: #159947;
  border: 1px solid $light-green;
  cursor: pointer;
  transition: all 0.5s;

  &:hover {
    background: #ffffff;
    color: $light-green;
  }

  &:disabled {
    background: rgba(21, 153, 71, 0.2);
    border: 1px solid rgba(21, 153, 71, 0.2);
    color: rgba(255, 255, 255, 0.2);
    cursor: not-allowed;
  }

  @include width-1200 {
    font-size: 16px;
    padding: 14px 0;
  }
}

.btn-white {
  display: block;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: none;
  border: none;
  padding: 18px 0;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: #06373A;
  background: #FFFFFF;
  border: 1px solid #FFFFFF;
  cursor: pointer;
  transition: all 0.5s;

  &:hover {
    background: #1F5F5B;
    color: #ffffff;
  }

  &:disabled {
    background: rgba(31, 95, 91, 0.2);
    border: 1px solid rgba(31, 95, 91, 0.2);
    color: rgba(255, 255, 255, 0.2);
    cursor: not-allowed;
  }

  @include width-1200 {
    font-size: 16px;
    padding: 14px 0;
  }
}

.text-btn {
  border: none;
  background: none;
  padding: 0;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  letter-spacing: 0.06em;
  text-transform: uppercase;
  color: #159947;
  cursor: pointer;
  display: flex;
  align-items: center;
  transition: all 0.4s;

  @include width-1200 {
    font-size: 16px;
  }
  & svg {
    fill: #159947;
    transition: all 0.4s;
  }

  &:hover {
    color: $dark-green;
    & svg {
      fill: $dark-green;
    }
  }
}

.title {
  &-h1 {
    font-weight: 300;
    font-size: 90px;
    line-height: 130%;
    letter-spacing: -0.03em;
    text-transform: uppercase;
    color: #061A23;
    @include width-1400 {
      font-size: 80px;
    }
    @include width-1200 {
      font-size: 70px;
      line-height: 110%;
    }
    @include width-768 {
      font-size: 45px;
      line-height: 105%;
    }
  }
  &-h2 {
    font-weight: 300;
    font-size: 140px;
    line-height: 100%;
    letter-spacing: -0.05em;
    color: #1F5F5B;
    padding-top: 20px;

    @include width-1400 {
      font-size: 120px;
      padding-top: 10px;
    }
    @include width-1200 {
      font-size: 80px;
      padding-top: 1.5%;
    }
    @include width-960 {
      padding-top: 5px;
    }
    @include width-768 {
      font-size: 50px;
      text-align: center;
      padding: 35px 0;
    }
  }
  &-h3 {
    font-weight: 300;
    font-size: 45px;
    line-height: 115%;
    color: #061A23;

    @include width-1400 {
      font-size: 36px;
    }
    @include width-1200 {
      font-size: 28px;
    }
    @include width-768 {
      font-size: 24px;
    }
  }
}

.input {
  position: relative;
  &__placeholder {
    font-weight: 400;
    font-size: 24px;
    line-height: 29px;
    font-feature-settings: 'pnum' on, 'lnum' on;
    color: #FFFFFF;
    position: absolute;
    left: 23px;
    top: 50%;
    transform: translateY(-50%);
    transition: all 0.5s;
    pointer-events: none;

    &.focused {
      color: #B4D0CE;
      font-size: 18px;
      left: 5px;
      top: -10px;
      transform: translateY(0);
    }
  }
}

.tabs {
  @include tabs();
  @include width-768 {
  }
  & .tabs__nav-btn--active {
    background-color: transparent;
    border: 1px solid #159947;
    color: #159947;
  }
  &__panel {
    position: relative;
  }
  &__nav {
    width: 100%;
    display: flex;
    justify-content: space-between;
    background: #ffffff;
    &-item {
      width: 100%;
    }
    &-btn {
      background: none;
      border: none;
      width: 100%;
      font-weight: 300;
      font-size: 28px;
      line-height: 125%;
      color: #061A23;
      cursor: pointer;
      padding: 27px 0;
      border: 1px solid transparent;
      transition: all 0.5s;
      display: flex;
      align-items: center;
      justify-content: center;
      white-space: nowrap;
      &:hover {
        border: 1px solid #159947;
        color: #159947;
      }
      &.active {
        border: 1px solid #159947;
        color: #159947;
      }

      @include width-1400 {
        font-size: 26px;
      }
      @include width-1200 {
        font-size: 18px;
        padding: 16px 0;
      }
    }
  }
}

.page {

  &__hat {
    margin-top: 32px;
  }

  &__breadcrumbs {
    margin-bottom: 50px;

    @include width-1200 {
      margin-bottom: 40px;
    }
    & a {
      font-weight: 300;
      font-size: 18px;
      line-height: 27px;
      letter-spacing: 0.02em;
      color: #061A23;
      &:not(:last-child) {
        position: relative;
        &::after {
          content: '//';
          margin-left: 20px;
          margin-right: 20px;
        }
      }
      &:last-child {
        color: #159947;
      }

      @include width-1400 {
        font-size: 16px;
      }
      @include width-768 {
        font-size: 14px;
      }
    }
  }

  &__wrap {
    background: #F9F9F9;
    padding: 50px 0 80px;
    margin-top: 50px;

    @include width-1200 {
      margin-top: 40px;
    }
  }
}
