.footer {
  background: #1F5F5B;
  padding: 80px 0;
  color: #ffffff;

  @include width-1200 {
    padding: 50px 0;
  }
  @include width-768 {
    padding: 25px 0;
  }

  &__logo {
    max-width: 760px;
    margin: 0 auto;

    @include width-1200 {
      max-width: 460px;
    }
    @include width-768 {
      max-width: 230px;
    }
  }

  &__builder {
    display: block;
    font-weight: 300;
    font-size: 28px;
    line-height: 125%;
    text-align: center;
    color: #FFFFFF;
    margin-top: 20px;

    @include width-1200 {
      font-size: 24px;
    }
    @include width-768 {
      font-size: 20px;
    }
  }

  &__left {
    display: flex;
    flex-direction: column;

    @include width-1200 {
      flex-direction: row;
      justify-content: space-between;
    }
    @include width-768 {
      flex-direction: column;
    }
  }

  &__mlinks {
    display: flex;
    flex-direction: column;
    margin-left: -9px;
    margin-top: -16px;

    @include width-1200 {
      max-width: 350px;
    }
    @include width-768 {
      max-width: 100%;
      margin-left: 0;
    }
  }

  &__mlink {
    font-weight: 400;
    font-size: 28px;
    line-height: 125%;
    padding: 16px 8px;
    padding-right: 40px;
    transition: all 0.4s;
    position: relative;
    border: 1px solid transparent;
    &::after {
      content: '';
      position: absolute;
      right: 8px;
      top: 0;
      bottom: 0;
      margin: auto 0;
      width: 17px;
      height: 17px;
      background: url('../img/icons/arrow_right_up.svg') no-repeat;
      background-size: 17px;
      opacity: 0;
      visibility: hidden;
      transition: all 0.4s;

      @include width-768 {
        width: 13px;
        height: 13px;
        background-size: 13px;
      }
    }
    &:hover {
      border: 1px solid #B4D0CE;
      &::after {
        opacity: 1;
        visibility: visible;
      }
    }

    @include width-1200 {
      font-size: 24px;
    }
    @include width-768 {
      font-size: 20px;
    }
  }

  &__slinks {
    display: grid;
    grid-template-columns: auto auto;
    column-gap: 90px;
    row-gap: 30px;
    margin-top: 60px;
    height: fit-content;

    @include width-1200 {
      margin-top: 0;
      row-gap: 25px;
    }
    @include width-768 {
      margin-top: 40px;
    }
  }

  &__slink {
    font-weight: 300;
    font-size: 20px;
    line-height: 24px;
    @include hover-link(#ffffff);

    @include width-1200 {
      font-size: 16px;
    }
  }

  &__right {
    max-width: 370px;

    @include width-1200 {
      display: grid;
      grid-template-columns: auto auto;
      column-gap: 110px;
      max-width: 100%;
      width: fit-content;
      row-gap: 32px;
    }
    @include width-768 {
      grid-template-columns: 1fr;
    }
  }
}

.footer-info {
  padding: 40px 0;
  border-top: 1px solid #B4D0CE;
  border-bottom: 1px solid #B4D0CE;
  margin-top: 40px;
  display: grid;
  grid-template-columns: 560px auto;
  justify-content: space-between;
  gap: 20px;

  @include width-1200 {
    margin-top: 32px;
    grid-template-columns: 1fr;
    gap: 50px;
  }
}

.footer-item {
  width: 100%;
  display: flex;
  flex-direction: column;

  &:not(:last-child) {
    margin-bottom: 50px;
  }

  @include width-1200 {
    &:not(:last-child) {
      margin-bottom: 0;
    }
  }
  &__title {
    display: block;
    margin-bottom: 16px;
    font-weight: 300;
    font-size: 20px;
    line-height: 24px;
    color: #B4D0CE;

    @include width-1200 {
      font-size: 16px;
      margin-bottom: 8px;
    }
  }

  &__subtitle {
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;

    @include width-1200 {
      font-size: 16px;
    }
  }

  @include width-1200 {
    max-width: 280px;
  }
}

.footer-copy {
  font-weight: 300;
  font-size: 16px;
  line-height: 19px;
  color: #B4D0CE;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 40px;

  @include width-1200 {
    flex-direction: column;
    margin-top: 32px;
  }
  &__left {
    display: flex;
    flex-shrink: 0;

    @include width-1200 {
      width: 100%;
      justify-content: space-between;
      margin-top: 40px;
      order: 2;
    }
    @include width-768 {
      flex-direction: column;
      text-align: center;
    }
    & a {
      margin-left: 70px;
      transition: 0.4s;
      &:hover {
        color: #ffffff;
        transition: 0.4s;
      }
      @include width-768 {
        margin-left: 0;
        margin-top: 32px;
      }
    }
  }
  &__right {
    display: block;
    max-width: 685px;
    margin-left: 60px;

    @include width-1400 {
      max-width: 430px;
    }
    @include width-1200 {
      max-width: 100%;
      margin-left: 0;
    }
  }
}