.header {
  padding: 24px 0;

  @include width-768 {
    padding: 15px 0;
  }
  &__content {
    display: grid;
    align-items: center;
    justify-items: start;
    grid-template-areas: 'menu logo btn';
    grid-template-columns: 400px 1fr 400px;

    @include width-1200 {
      grid-template-areas: 'menu logo' 'btn btn';
      // grid-template-columns: 1fr;
      grid-template-columns: auto auto;
    }
    @include width-768 {
      grid-template-areas: 'logo' 'menu' 'btn';
      grid-template-columns: 1fr;
    }
  }

  &__tel {
    font-weight: 300;
    font-size: 24px;
    line-height: 29px;
    font-feature-settings: 'pnum' on, 'lnum' on;
    color: #222125;
    margin-left: 55px;
    transition: all 0.3s;
    &:hover {
      color: #159947;
    }

    @include width-1400 {
      font-size: 20px;
    }
    @include width-1200 {
      font-size: 18px;
      margin-left: 120px;
    }
    @include width-768 {
      margin-left: 20px;
    }
  }

  &__logo {
    grid-area: logo;
    justify-self: center;
    max-width: 370px;

    @include width-1200 {
      justify-self: end;
      max-width: 260px;
      margin-left: 40px;
    }
    @include width-768 {
      justify-self: center;
      margin-left: 0;
      max-width: 225px;
    }
  }

  &__btn {
    grid-area: btn;
    justify-self: end;

    @include width-1200 {
      justify-self: center;
      margin-top: 25px;
    }
    @include width-768 {
      max-width: 220px;
    }
    @include width-768 {
      margin-top: 15px;
    }
  }

  &__menu {
    grid-area: menu;
    display: flex;
    cursor: pointer;

    @include width-768 {
      width: 100%;
      margin-top: 20px;
      justify-content: space-between;
    }
  }
}

.header-burger {
  & span {
    display: block;
    font-weight: 300;
    font-size: 24px;
    line-height: 29px;
    font-feature-settings: 'pnum' on, 'lnum' on;
    color: #222125;
    position: relative;
    padding-left: 60px;
    transition: all 0.3s;
    &::after {
      content: '';
      position: absolute;
      top: 5px;
      left: 0;
      height: 2px;
      width: 30px;
      background: #222125;
      transition: all 0.3s;

      @include width-768 {
        width: 20px;
        top: 9px;
      }
    }
    &::before {
      content: '';
      position: absolute;
      bottom: 7px;
      left: 0;
      height: 2px;
      width: 45px;
      background: #222125;
      transition: all 0.3s;

      @include width-768 {
        width: 35px;
        bottom: 9px;
      }
    }
    &:hover {
      color: #159947;
      &::before {
        background: #159947;
      }
      &::after {
        background: #159947;
      }
    }

    @include width-1400 {
      font-size: 20px;
    }
    @include width-1200 {
      font-size: 18px;
    }
    @include width-768 {
      padding-left: 50px;
    }
  }
}
