// ---------------REQ----------------
.req {
  color: #ffffff;
  margin-top: 100px;
  &__title {
    text-transform: uppercase;
    margin-bottom: -25px;

    @include width-1400 {
      text-align: center;
    }
    @include width-1200 {
      margin-bottom: -13px;
    }
    @include width-768  {
      padding: 0;
      margin-bottom: -10px;
    }
  }
  &__back {
    background: #1F5F5B;
  }

  &__content {
    display: grid;
    grid-template-areas: 'subtitle img' 'form img';
    grid-template-columns: auto 1fr;
    grid-template-rows: auto 1fr;
    padding-left: max(80px, calc((100% - 1400px) / 2));
    column-gap: 80px;
    align-items: flex-start;

    @include width-1400 {
      padding: 0 80px;
      column-gap: 32px;
    }
    @include width-1200 {
      grid-template-areas: 'subtitle img' 'form form';
      grid-template-columns: 1fr 1fr;
      padding: 40px;
    }
    @include width-768 {
      grid-template-areas: 'img' 'subtitle' 'form';
      grid-template-columns: 1fr;
    }
  }

  &__subtitle {
    display: block;
    font-weight: 300;
    font-size: 28px;
    line-height: 125%;
    margin-bottom: 64px;
    grid-area: subtitle;
    max-width: 620px;
    margin-top: 100px;

    @include width-1400 {
      max-width: 540px;
      margin-bottom: 32px;
      margin-top: 80px;
    }
    @include width-1200 {
      font-size: 24px;
    }
    @include width-768 {
      text-align: center;
      justify-self: center;
      margin-top: 8px;
      margin-bottom: 16px;
    }
  }

  &__form {
    width: 100%;
    grid-area: form;
    max-width: 565px;

    @include width-1400 {
      max-width: 540px;
      margin-bottom: 80px;
    }
    @include width-1200 {
      justify-self: center;
      max-width: 450px;
      margin-top: 32px;
      margin-bottom: 0;
    }
    & .input {
      &:not(:first-child) {
        margin-top: 36px;
        @include width-1200 {
          margin-top: 16px;
        }
      }
    }
    & input {
      border: none;
      background: none;
      padding: 0;
      border-bottom: 2px solid #FFFFFF;
      font-weight: 400;
      font-size: 24px;
      line-height: 29px;
      font-feature-settings: 'pnum' on, 'lnum' on;
      color: #ffffff;
      height: 80px;
      padding: 0 23px;
      width: 100%;
      border-radius: 0;
    }
    & button {
      margin-top: 56px;

      @include width-1400 {
        margin-top: 32px;
      }
    }
  }

  &__agree {
    display: block;
    font-weight: 300;
    font-size: 20px;
    line-height: 24px;
    color: #B4D0CE;
    margin-top: 16px;

    @include width-1200 {
      font-size: 16px;
    }
    @include width-768 {
      font-size: 14px;
      line-height: 17px;
    }
    & a {
      text-decoration: underline;
      transition: all 0.4s;
      &:hover {
        color: #ffffff;
      }
    }
  }

  &__img {
    grid-area: img;
    height: 770px;
    & img {
      object-fit: cover;
      height: 100%;
      width: 100%;
    }

    @include width-1400 {
      height: auto;
      align-self: center;
      & img {
        object-fit: fill;
      }
    }
  }
}
// ---------------/REQ----------------
