@mixin hover-link($color: #061A23) {
  position: relative;
  width: fit-content;
  &::after {
    content: '';
    position: absolute;
    bottom: -4px;
    left: 0;
    height: 2px;
    width: 0%;
    background: $color;
    opacity: 0;
    visibility: hidden;
    transition: 0.4s;
  }
  &:hover {
    &::after {
      width: 100%;
      opacity: 1;
      visibility: visible;
      bottom: -4px;
      transition: 0.4s;
    }
  }
}