.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
  background: #ffffff;
  opacity: 0;
  visibility: hidden;
  transition: 0.3s;
  &.active {
    opacity: 1;
    visibility: visible;
    transition: 0.3s;
  }
  &-close {
    position: absolute;
    right: 50px;
    top: 40px;
    & img {
      width: 60px;
      height: 60px;
    }
  }
}

.menu {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
  background: #ffffff;
  transform: translateY(-100%);
  transition: 0.5s;
  overflow-y: scroll;
  &.menu--active {
    transform: translateY(0);
  }

  & .footer__slink {
    @include hover-link;
  }
  & .footer__slinks {
    margin-right: 50px;

    @include width-1200 {
      margin-left: 150px;
      grid-template-columns: 1fr;
    }
    @include width-768 {
      margin-left: 0;
      margin-right: 0;
      margin-top: 32px;
    }
  }
  @include width-1200 {
    & .footer__left {
      justify-content: flex-start;
    }
  }

  &__container {
    padding: 0 80px;
    @include width-1200 {
      padding: 0 40px;
    }
    @include width-768 {
      padding: 0 20px;
    }
  }
  &__close {
    background: none;
    border: none;
    padding: 0;
    font-weight: 300;
    font-size: 20px;
    line-height: 24px;
    color: #222125;
    position: absolute;
    top: 40px;
    display: flex;
    align-items: center;
    cursor: pointer;
    z-index: 10;
    transition: all 0.3s;
    & svg {
      stroke: #061A23;
      stroke-width: 2px;
      width: 40px;
      height: 40px;
      margin-right: 15px;
      transition: all 0.3s;
    }
    &:hover {
      color: #159947;
      & svg {
        stroke: #159947;
      }
    }

    @include width-1400 {
      font-size: 18px;
    }
    @include width-1200 {
      font-size: 16px;
    }
    @include width-768 {
      top: 55px;
      font-size: 14px;

      & svg {
        margin-right: 10px;
      }
    }
  }

  &__content {
    padding: 160px 0;

    @include width-1400 {
      padding: 110px 0;
    }
    @include width-1200 {
      padding: 40px 0;
    }
    @include width-768 {
      padding: 20px 0 60px;
    }
  }

  &__hat {
    display: grid;
    grid-template-areas: 'logo info'
                         'links info';
    justify-content: space-between;
    gap: 40px;

    @include width-1200 {
      grid-template-areas: 'logo'
                           'links'
                           'info';
      grid-template-columns: 1fr;
      gap: 0;
    }
  }

  &__logo {
    grid-area: logo;
    margin-bottom: 50px;
    max-width: 460px;

    @include width-1200 {
      justify-self: flex-end;
      max-width: 380px;
    }
    @include width-768 {
      justify-self: center;
      max-width: 230px;
      margin-bottom: 80px;
    }
  }
  &__links {
    grid-area: links;
    width: 560px;

    @include width-1200 {
      width: 100%;
    }
  }
  &__info {
    grid-area: info;
    & .footer-item__title {
      color: #989898;
    }

    @include width-1200 {
      margin-top: 80px;
    }
    @include width-768 {
      margin-top: 40px;
    }
  }

  &__footer {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;

    @include width-1200 {
      margin-top: 100px;
    }
    @include width-768 {
      flex-direction: column;
      align-items: center;
      margin-top: 35px;
    }
  }

  &__btn {
    flex-shrink: 0;
    & svg {
      margin-left: 15px;
      width: 20px;
      height: 20px;
    }
  }

  &__request {
    width: 100%;
    max-width: 370px;
    & span {
      font-weight: 300;
      font-size: 20px;
      line-height: 130%;
      color: #061A23;
    }
    & button {
      margin-top: 16px;
      max-width: 100%;
    }

    @include width-1200 {
      margin-left: 135px;
    }
    @include width-768 {
      margin-left: 0;
      max-width: 100%;
      margin-top: 32px;
    }
  }
}
