/* MAIN PAGE */
// ---------------INTRO----------------
.intro-menu {
  width: fit-content;
  margin: 0 auto;
  margin-top: 10px;
  & ul {
    display: flex;
    & li {
      font-weight: 300;
      font-size: 20px;
      line-height: 24px;
      color: #000000;
      &:not(:last-child) {
        margin-right: 60px;
      }
      @include hover-link;

      @include width-1400 {
        &:not(:last-child) {
          margin-right: 50px;
        }
      }
    }
  }

  @include width-1200 {
    display: none;
  }
}

.intro {

  &__title {
    margin-top: 50px;
    text-align: center;
  }

  &__points {
    margin-top: 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    & * {
      &:not(:last-child) {
        margin-right: 30px;
      }
    }

    @include width-1200 {
      flex-direction: column;
      margin-top: 40px;
      & * {
        &:not(:last-child) {
          margin-right: 0;
          margin-bottom: 16px;
        }
      }
    }
    & span {
      font-weight: 300;
      font-size: 28px;
      line-height: 125%;
      color: #159947;
      flex-shrink: 0;
      @include width-1200 {
        font-size: 24px;
      }
      @include width-768 {
        font-size: 20px;
      }
    }
    & div {
      height: 1px;
      width: 100%;
      max-width: 250px;
      background: #159947;

      @include width-1400 {
        max-width: 120px;
      }
      @include width-1200 {
        max-width: 60px;
      }
    }
  }

  &__img {
    margin-top: 24px;
    position: relative;

    @include width-768 {
      height: 355px;
      width: 100%;
      & img {
        object-fit: cover;
        height: 100%;
        width: 100%;
      }
    }
  }

  &__citybox {
    font-weight: 400;
    font-size: 28px;
    line-height: 125%;
    color: #06373A;
    background: #ffffff;
    position: absolute;
    top: 64px;
    left: 0;
    padding: 48px 60px 80px 48px;
    & img {
      position: absolute;
      bottom: 0;
      right: 0;
    }

    @include width-1400 {
      font-size: 26px;
    }
    @include width-1200 {
      font-size: 24px;
      top: 32px;
    }
    @include width-768 {
      font-size: 20px;
      padding: 32px 32px 60px;
      top: 24px;
      & img {
        max-width: 130px;
      }
    }
  }

  &__stats {
    margin-top: 50px;
    display: grid;
    grid-template-columns: repeat(4,1fr);

    @include width-1200 {
      margin-top: 40px;
    }
    @include width-768 {
      grid-template-columns: 1fr 1fr;
    }
  }

  &__stat {
    display: flex;
    flex-direction: column;
    border: 1px solid #B4D0CE;
    padding: 24px 24px 60px;

    @include width-1200 {
      padding: 16px 16px 36px;
    }
    @include width-768 {
      padding: 16px 16px 50px;
    }
    & :first-child {
      font-weight: 400;
      font-size: 90px;
      line-height: 130%;
      color: #1F5F5B;

      @include width-1200 {
        font-size: 70px;
      }
      @include width-768 {
        font-size: 45px;
      }
    }
    & :last-child {
      font-weight: 300;
      font-size: 24px;
      line-height: 135%;
      color: #1F5F5B;

      @include width-768 {
        font-size: 14px;
      }
    }
  }
}
// ---------------/INTRO----------------

// ---------------ABOUT----------------
.about {
  margin-top: 100px;

  &__content {
    margin-top: 100px;

    @include width-1400 {
      margin-top: 50px;
    }
    @include width-1200 {
      margin-top: 60px;
    }
    @include width-768 {
      margin-top: 40px;
    }
  }

  &__line {
    display: flex;

    @include width-1200 {
      flex-direction: column;
      &:last-child {
        margin-top: 32px;
      }
    }
  }
  &__btn {
    margin-top: 32px;

    @include width-1400 {
      margin-top: 16px;
    }
    @include width-768 {
      max-width: 100%;
    }
  }
  &__text {
    width: 50%;
    display: flex;
    flex-direction: column;
    padding: 135px;
    padding-bottom: 110px;

    @include width-1400 {
      padding: 60px 70px;
      padding-bottom: 0;
    }
    @include width-1200 {
      width: 100%;
      padding: 0 40px;
      order: 1;
      margin-top: 24px;
    }
    @include width-768 {
      padding: 0 20px;
    }
    & img {
      width: 80px;
      height: 80px;
      margin-bottom: 32px;

      @include width-1400 {
        margin-bottom: 16px;
        width: 60px;
        height: 60px;
      }
      @include width-1200 {
        width: 40px;
        height: 40px;
      }
    }
    & h3 {
      margin-bottom: 32px;

      @include width-1400 {
        margin-bottom: 16px;
      }
    }
    & span {
      font-weight: 300;
      font-size: 24px;
      line-height: 135%;
      color: #061A23;

      @include width-1400 {
        font-size: 22px;
        line-height: 120%;
      }
      @include width-1200 {
        font-size: 16px;
        line-height: 135%;
      }
      @include width-768 {
        font-size: 14px;
      }
    }
    &-left {
      padding-left: max(80px, calc((100% - 1400px) / 2));

      @include width-1200 {
        padding-left: 40px;
      }
      @include width-768 {
        padding-left: 20px;
      }
    }
    &-right {
      padding-right: max(80px, calc((100% - 1400px) / 2));

      @include width-1200 {
        padding-right: 40px;
      }
      @include width-768 {
        padding-right: 20px;
      }
    }
  }

  &__img {
    width: calc(100% - 50%);
    height: 800px;
    & img {
      object-fit: cover;
      height: 100%;
      width: 100%;
      object-position: center;
    }

    @include width-1400 {
      height: 533px;
    }
    @include width-1200 {
      width: 100%;
      order: 0;
      padding: 0 40px;
    }
    @include width-768 {
      padding: 0 20px;
      height: 280px;
    }
  }
}
// ---------------/ABOUT----------------

// ---------------ESTIMATE----------------
.estimate {
  margin-top: 80px;

  @include width-1400 {
    margin-top: 60px;
  }

  &__content {
  }

  &__title {
    text-align: center;
    font-weight: 300;
    font-size: 140px;
    line-height: 100%;
    letter-spacing: -0.05em;
    text-transform: uppercase;
    color: #1F5F5B;
    position: relative;
    z-index: 2;

    @include width-1400 {
      font-size: 120px;
      line-height: 85%;
    }
    @include width-1200 {
      font-size: 80px;
      line-height: 100%;
    }
    @include width-768 {
      font-size: 50px;
      line-height: 100%;
    }
  }

  &__img {
    position: relative;
    z-index: 1;
    margin-top: -60px;

    @include width-768 {
      margin-top: -30px;
    }
  }
  &__like {
    position: absolute;
    top: 20%;
    left: 0;
    right: 0;
    margin: 0 auto;
    width: 100px;
    height: 88px;

    @include width-1400 {
      width: 80px;
      height: 70px;
    }
    @include width-1200 {
      width: 50px;
      height: 44px;
    }
    @include width-768 {
      width: 27px;
      height: 24px;
    }
  }
}
// ---------------/ESTIMATE----------------

// ---------------BUILDER----------------
.builder {
  margin-top: 150px;

  @include width-1200 {
    margin-top: 100px;
  }
  @include width-768 {
    margin-top: 80px;
  }
  &__content {
    margin-top: 100px;

    @include width-1200 {
      margin-top: 60px;
    }
    @include width-768 {
      margin-top: 40px;
    }
  }

  &-hat {
    display: grid;
    grid-template-areas: 'pic1 logo pic2';
    justify-content: space-between;
    align-items: flex-end;

    @include width-768 {
      grid-template-areas: 'logo logo' 'pic1 pic2';
      column-gap: 40px;
    }
    &__pic {
      max-width: 320px;

      @include width-1400 {
        max-width: 200px;
      }
      @include width-1200 {
        max-width: 130px;
      }
      &-first {
        grid-area: pic1;
      }
      &-sec {
        grid-area: pic2;
        justify-self: flex-end;
      }
    }
  }

  &-footer {
    display: grid;
    grid-template-areas: 'picc1 picc2'
                         'picc1 btn';
    grid-template-columns: auto auto;
    justify-content: space-between;

    @include width-1200 {
      grid-template-areas: 'picc1 picc2' 'btn btn';
      column-gap: 40px;
    }
    &__pic {
      max-width: 440px;
      position: relative;

      @include width-1400 {
        max-width: 310px;
      }
      @include width-1200 {
        max-width: 215px;
      }
      @include width-768 {
        max-width: 160px;
      }
      &-first {
        grid-area: picc1;
        &::after {
          content: '';
          position: absolute;
          width: 88px;
          height: 88px;
          left: -40px;
          top: 150px;
          background: url('../img/icons/hearts.svg') no-repeat;
          background-size: 88px;

          @include width-1400 {
            top: 100px;
            height: 68px;
            width: 68px;
            background-size: 68px;
          }
          @include width-1200 {
            width: 48px;
            height: 48px;
            background-size: 48px;
            left: -20px;
            top: 80px;
          }
          @include width-768 {
            width: 28px;
            height: 28px;
            background-size: 28px;
            left: -10px;
            top: 60px;
          }
        }
      }
      &-sec {
        grid-area: picc2;
        justify-self: flex-end;
        &::after {
          content: '';
          position: absolute;
          width: 88px;
          height: 88px;
          left: -40px;
          top: 20px;
          background: url('../img/icons/keys.svg') no-repeat;
          background-size: 88px;

          @include width-1400 {
            top: 40px;
            height: 68px;
            width: 68px;
            background-size: 68px;
          }
          @include width-1200 {
            width: 48px;
            height: 48px;
            background-size: 48px;
            left: -20px;
          }
          @include width-768 {
            width: 28px;
            height: 28px;
            background-size: 28px;
            left: -10px;
            top: 20px;
          }
        }
      }
    }
    &__btn {
      grid-area: btn;
      margin-top: auto;
      justify-self: flex-end;
      & svg {
        width: 17px;
        height: 17px;
        margin-left: 20px;
      }

      @include width-1200 {
        justify-self: center;
        margin-top: 40px;
      }
    }
  }

  &__logo {
    grid-area: logo;
    align-self: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 650px;

    @include width-1200 {
      max-width: 420px;
      margin-bottom: 40px;
    }
    @include width-768 {
      justify-self: center;
      max-width: 335px;
      margin-bottom: 8px;
    }
    & img {
      @include width-1400 {
        width: 80px;
        height: 80px;
      }
      @include width-768 {
        width: 40px;
        height: 40px;
      }
    }
    & span {
      font-weight: 300;
      font-size: 28px;
      line-height: 125%;
      text-align: center;
      color: #1F5F5B;

      @include width-1200 {
        font-size: 24px;
      }
      @include width-768 {
        font-size: 16px;
        line-height: 110%;
      }
    }
  }

  &__text {
    font-weight: 300;
    font-size: 90px;
    line-height: 95%;
    text-align: center;
    letter-spacing: -0.02em;
    color: #1F5F5B;

    @include width-1400 {
      font-size: 78px;
    }
    @include width-1200 {
      font-size: 48px;
      line-height: 100%;
    }
    @include width-768 {
      font-size: 24px;
      margin: 10px 0;
    }
  }
}
// ---------------/BUILDER----------------

// ---------------PLACE----------------
.place {
  margin-top: 120px;

  @include width-768 {
    margin-top: 80px;
  }
  &__text {
    padding: 0;
    max-width: 580px;

    @include width-1400 {
      max-width: 480px;
    }
    &-first {
      order: 0;

      @include width-768 {
        order: 1;
      }
    }
    &-sec {
      padding: 45px 0 0 130px;

      @include width-1400 {
        padding: 50px 0 0 70px;
      }
      @include width-1200 {
        padding: 0 0 0 20px;
      }
      @include width-768 {
        order: 0;
        padding: 0;
        margin-bottom: 24px;
        margin-top: 0;
      }
    }
    & h3 {
      & span {
        color: #159947;
        font-weight: 300;
        font-size: inherit;
        line-height: inherit;
      }
    }
    & span {
      & p:not(:last-child) {
        display: block;
        margin-bottom: 16px;
      }
    }
  }
  &__line {
    display: flex;
    padding-left: max(80px, calc((100% - 1400px) / 2));
    margin-top: 100px;

    @include width-1400 {
      margin-top: 80px;
    }
    @include width-1200 {
      padding: 0 40px;
      justify-content: space-between;
      margin-top: 60px;
    }
    @include width-768 {
      margin-top: 40px;
      flex-direction: column;
      padding: 0 20px;
    }
  }
  &__content {
    display: flex;

    @include width-1200 {
      margin-top: 40px;
    }
    @include width-768 {
      flex-direction: column;
      margin-top: 24px;
    }
  }
  &__left {
    width: 580px;
    flex-shrink: 0;
    padding: 0;
    align-self: center;
    &-first {
      padding-right: 16px;
    }

    @include width-1400 {
      width: 460px;
    }
    @include width-1200 {
      width: 100%;
      max-width: 50%;
      padding-right: 20px;
    }
    @include width-768 {
      max-width: 100%;
      padding-right: 0;
    }
  }
  &__img {
    width: calc(100% - 580px);
    height: 760px;

    @include width-1400 {
      width: calc(100% - 460px);
      height: 600px;
    }
    @include width-1200 {
      height: 270px;
      width: 100%;
      max-width: 50%;
    }
    @include width-768 {
      max-width: 100%;
      order: 0;
    }
    & img {
      object-fit: cover;
      height: 100%;
      width: 100%;
      object-position: center;
    }
    &-left {
      @include width-1200 {
        height: 270px;
        width: 100%;
        max-width: 50%;
      }
      @include width-768 {
        max-width: 100%;
        order: 1;
      }
      & img {
        object-fit: cover;
        height: 100%;
        width: 100%;
        object-position: center;
      }
    }
  }
}
// ---------------/PLACE----------------

// ---------------STAT----------------
.stat {
  margin-top: 50px;

  @include width-1200 {
    margin-top: 40px;
  }
  &__items {
    display: grid;
    &-first {
      grid-template-columns: 1fr 1fr;

      @include width-768 {
        grid-template-columns: 1fr;
      }
    }
    &-sec {
      grid-template-columns: 1fr 1fr 1fr;

      @include width-768 {
        grid-template-columns: 1fr;
      }
    }

    & .intro__stat {
      & :last-child {
        @include width-1200 {
          font-size: 16px;
        }
        @include width-768 {
          font-size: 14px;
        }
      }

      @include width-768 {
        padding: 24px 16px;
      }
    }
  }
}
// ---------------/STAT----------------

// ---------------WAYS----------------
.ways {
  margin-top: 80px;

  @include width-1400 {
    margin-top: 70px;
  }
  &__hat {
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include width-1200 {
      align-items: flex-start;
    }
    @include width-768 {
      flex-direction: column;
    }
  }

  &__title {
    color: #159947;
    padding: 0;

    @include width-768 {
      align-self: center;
      margin-bottom: 24px;
    }
  }

  &__list {
    width: 100%;
    max-width: 835px;
    margin-left: 60px;

    @include width-1400 {
      max-width: 600px;
    }
    @include width-768 {
      margin-left: 0;
      max-width: 100%;
    }
    & ul {
      & li {
        font-weight: 400;
        font-size: 24px;
        line-height: 125%;
        color: #061A23;
        position: relative;
        display: flex;
        align-items: flex-start;
        border-bottom: 2px solid transparent;
        border-image: linear-gradient(90deg, #4AB265 0%, rgba(74, 178, 101, 0) 100%);
        border-image-slice: 1;
        padding: 16px 24px;

        @include width-768 {
          font-size: 20px;
        }
        & svg {
          fill: #49B265;
          width: 16px;
          height: 12px;
          margin-right: 12px;
          flex-shrink: 0;
          margin-top: 9px;
        }
      }
    }
  }

  &__body {
    margin-top: 50px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    position: relative;

    @include width-1400 {
      grid-template-columns: 430px 1fr;
    }
    @include width-1200 {
      grid-template-columns: 330px 1fr;
    }
    @include width-768 {
      grid-template-columns: 1fr;
    }
  }

  &__right {
    border-top: 1px solid #1F5F5B;
    border-bottom: 1px solid #1F5F5B;
    border-right: 1px solid #1F5F5B;
    display: flex;
    align-items: center;

    @include width-768 {
      border-left: 1px solid #1F5F5B;
      border-top: none;
    }
  }
  &__text {
    height: fit-content;
    padding: 32px;
    max-width: 560px;

    @include width-1200 {
      padding-left: 16px;
    }
    @include width-768 {
      padding: 40px 16px;
    }
    & h3 {
      margin-bottom: 32px;

      @include width-1200 {
        margin-bottom: 16px;
      }
    }
    & span {
      font-weight: 300;
      font-size: 24px;
      line-height: 135%;
      color: #000000;

      @include width-1400 {
        font-size: 22px;
      }
      @include width-1200 {
        font-size: 16px;
      }
    }
  }
  &__btn {
    margin-top: 35px;

    @include width-1200 {
      margin-top: 16px;
    }
    & svg {
      width: 17px;
      height: 17px;
      margin-left: 20px;
    }
  }
  &__img {
    position: relative;
    height: 460px;

    @include width-1400 {
      height: 370px;
    }
    @include width-1200 {
      height: 286px;
    }
    @include width-768 {
      height: auto;
    }
    & picture img {
      object-fit: cover;
      height: 100%;
      width: 100%;
      object-position: bottom;
    }
  }
  &__icon {
    position: absolute;
    bottom: -50px;
    right: -50px;
    width: 100px;
    height: 100px;

    @include width-1400 {
      width: 80px;
      height: 80px;
      bottom: -40px;
      right: -40px;
    }
    @include width-1200 {
      height: 60px;
      width: 60px;
      right: -30px;
      bottom: -30px;
    }
    @include width-768 {
      right: 0;
      left: 0;
      margin: 0 auto;
      bottom: -25px;
    }
  }
}
// ---------------/WAYS----------------

// ---------------NEWS----------------
.news {
  margin-top: 140px;

  @include width-1400 {
    margin-top: 100px;
  }
  @include width-1200 {
    margin-top: 80px;
  }
  &__content {
    margin-top: 140px;
    display: flex;
    flex-direction: column;
    @include width-1400 {
      margin-top: 100px;
    }
    @include width-1200 {
      margin-top: 80px;
    }
    @include width-768 {
      margin-top: 60px;
    }
  }

  &__cards {
    display: grid;
    grid-template-columns: repeat(3,1fr);
    justify-items: space-between;
    gap: 60px;

    @include width-1200 {
      grid-template-columns: 1fr;
      gap: 40px;
    }
  }

  &__btn {
    margin-top: 50px;
    align-self: flex-end;

    @include width-1200 {
      align-self: center;
    }
    @include width-768 {
      margin-top: 40px;
    }
    & svg {
      width: 56px;
      height: 22px;
      margin-left: 16px;
    }
  }
}

.news-card {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto 100px 1fr;
  border-bottom: 1px solid #B4D0CE;
  padding-bottom: 25px;

  @include width-1400 {
    grid-template-rows: auto 140px 1fr;
  }
  @include width-1200 {
    grid-template-rows: 1fr;
  }
  &__hat {
    display: block;
    font-weight: 300;
    font-size: 20px;
    line-height: 24px;
    color: #1F5F5B;
    margin-bottom: 24px;

    @include width-1400 {
      font-size: 18px;
    }
    @include width-1200 {
      margin-bottom: 16px;
      font-size: 16px;
    }
    @include width-768 {
      font-size: 14px;
    }
  }

  &__hat-stock {
    color: #FF473C;
  }

  &__title {
    transition: all 0.4s;
    &:hover {
      text-decoration: underline;
    }
    & h4 {
      font-weight: 400;
      font-size: 28px;
      line-height: 125%;
      color: #061A23;

      @include width-1400 {
        font-size: 26px;
      }
      @include width-1200 {
        font-size: 24px;
      }
      @include width-768 {
        font-size: 20px;
      }
    }
  }

  &__title-stock {
    & h4 {
      color: #FF473C;
    }
  }

  &__footer {
    margin-top: auto;
  }

  &__subtitle {
    display: block;
    font-weight: 300;
    font-size: 16px;
    line-height: 19px;
    color: #989898;
    height: 100px;
    margin-top: 24px;
    @include width-1400 {
      height: 110px;
    }
    @include width-1200 {
      height: auto;
      margin-top: 16px;
    }
    @include width-768 {
      font-size: 14px;
    }
  }

  &__btn {
    margin-top: 20px;
    & svg {
      width: 17px;
      height: 17px;
      margin-left: 20px;
    }
  }
}
// ---------------/NEWS----------------

// ---------------MAP----------------
.map {
  margin-top: 50px;
  position: relative;
  &__nav {
    width: 100%;
    box-sizing: content-box;
    position: absolute;
    z-index: 2;
    top: -2px;
    left: 50%;
    transform: translateX(-50%);
    max-width: 1400px;

    & .tabs__nav {
      display: grid;
      grid-template-columns: repeat(5,1fr);
      @include width-768 {
        grid-template-columns: 1fr 1fr 1fr;
        & *:nth-child(4) {
          grid-column: span 2;
        }
      }
    }
  }
  &__body {
    width: 100%;
    height: 956px;
    position: relative;
    z-index: 1;

    @include width-1400 {
      height: 768px;
    }
    @include width-1200 {
      height: 870px;
    }
    @include width-768 {
      height: 560px;
    }
  }
}
// ---------------/MAP----------------

// ---------------MOTION----------------
.motion {
  overflow: hidden;
  margin-top: 140px;
  margin-bottom: 80px;

  @include width-1400 {
    margin-top: 120px;
    margin-bottom: 70px;
  }
  @include width-1200 {
    margin-top: 100px;
  }
  @include width-768 {
    margin-top: 80px;
  }
  &__content {
    margin-top: 120px;

    @include width-1400 {
      margin-top: 80px;
    }
    @include width-768 {
      margin-top: 60px;
    }
  }
  &__line {
    position: relative;
  }
  &__body {
    margin-top: 32px;

    @include width-768 {
      margin-top: 24px;
    }
  }
  &__swiper {
    // overflow: visible;

    & .swiper-slide {
      // opacity: 0.6;
    }
    & .active-slide {
      opacity: 1;
    }
    &-button {
      position: absolute;
      top: 0;
      bottom: 0;
      margin: auto 0;
      z-index: 4;
      width: 80px;
      height: 80px;
      background: #FFFFFF;
      border-radius: 50%;
      border: 1px solid #1F5F5B;
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;
      transition: all 0.5s;
      & svg {
        width: 16px;
        height: 27px;
        fill: #1F5F5B;
        transition: all 0.5s;
      }

      @include width-1400 {
        opacity: 0.6;
      }
      @include width-1200 {
        display: none;
      }
      &-next {
        right: -88px;

        @include width-1400 {
          right: -40px;
        }
      }
      &-prev {
        left: -88px;
        & svg {
          transform: rotate(180deg);
        }

        @include width-1400 {
          left: -40px;
        }
      }

      &:hover {
        background: #1F5F5B;
        & svg {
          fill: #ffffff;
        }
      }
    }
  }
  &__slide {
    & span {
      display: block;
      font-weight: 300;
      font-size: 24px;
      line-height: 135%;
      color: #061A23;
      margin-top: 32px;

      @include width-1200 {
        font-size: 16px;
        line-height: 135%;
      }
      @include width-768 {
        font-size: 14px;
        margin-top: 8px;
      }
    }
  }
}
// ---------------/MOTION----------------

