.house {

  &__hat {
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include width-1200 {
      flex-direction: column;
      align-items: flex-start;
    }
  }

  &__links {
    display: flex;
    flex-direction: column;

    @include width-1200 {
      width: 100%;
      flex-direction: row;
      justify-content: space-between;
      margin-top: 30px;
    }
    @include width-768 {
      flex-direction: column;
      margin-top: 70px;
    }
    & a {
      &:not(:last-child) {
        margin-bottom: 20px;
      }
      & svg {
        width: 17px;
        height: 17px;
        margin-left: 20px;
      }
    }
  }

  &__title {
    text-align: center;
  }

  &__img {
    margin-top: 50px;
    & svg {
      width: 100%;
      height: fit-content;
    }
    &-house {
      fill-opacity: 0;
      stroke-width: 0;
      cursor: pointer;
      transition: all 0.1s;
      &:hover {
        fill-opacity: 0.2;
        stroke-width: 15;
      }
    }
  }
}
